
.main {
  height: 10vh;
  width: 650px;
  background-color: white;
  position: relative;
  display: flex;
  justify-content: center;
  margin-left: 23vw;
  margin-top: 1vh;
  margin-bottom: 3vh;
  border-radius: 4vh;
  box-shadow: 0 0 2px rgba(75, 75, 75, 0.07);
  border: solid grey;
}

.nav {
  display: flex;
  align-items: center;
  justify-content: center;
}

.ulist {
  display: flex;
  list-style: none;
  justify-content: center;
  align-items: center;
  justify-content: space-between;


}

.ulist li {
  margin: 2vw;
  font-family: 'Times New Roman', Times, serif;

}

.maincard {
  height: 440px;
  width: 26vw;
  margin-bottom: 2vh;
  background-color: white;
  display: flex;
  /* flex-direction: row;
    flex-wrap: wrap; */
  margin-left: 10vh;
  padding: 3vh;
  border-radius: 1px;
  box-shadow: 0 0 5px rgba(75, 75, 75, 0.07);

}

.maincard img {
  height: 31vh;
  width: 18vw;
  margin-top: 1vh;
  margin-bottom: 1vw;

}

.maincard button {
  float: right;
  margin-top: 2vh;
  font-display: none;
}

.ulist li p {
  cursor: pointer;
  font-family: 'Times New Roman', Times, serif;

}

.ulist li p:hover {
  color: darkcyan;
  font-weight: 800;
}

.description {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-weight: 300;
  line-height: 22px;
  margin: 3px 0;
}

.item {
  font-family: "Cormorant Garamond", serif;
  font-size: 3vh;
  font-weight: 300;
  margin: 3px 0;
  text-transform: capitalize;
  letter-spacing: 0.25vh;
}
.dash{
  font-family: "Cormorant Garamond", serif;
  font-size: 1vw;
  font-weight: 300;
  margin: 3px 0;
}
.indexno{
  float: right;
  font-weight: lighter;
  font-size: 1.1vw;
  
}
.containor{
  display: grid;
  grid-row-gap: 2vw;
  grid-column-gap: 2vw;  /* specify row and column gap */
  grid-template-columns: auto auto auto;
}

@media only screen and (max-width: 900px) {
  .containor{
    display: grid;
    grid-row-gap: 2vw;
    grid-column-gap: 2vw;  /* specify row and column gap */
    grid-template-columns: auto auto;
  }
  .maincard {
    height: 590px;
    width: 300px;
  }
  .main {
    margin-left: 13vw;
  }
}
@media only screen and (max-width: 500px) {
  .containor{
    display: grid;
    grid-row-gap: 2vw;
    grid-column-gap: 2vw;  /* specify row and column gap */
    grid-template-columns: auto;
  }
  .maincard {
    height: 440px;
    width: 300px;
  }
  .main {
    margin-left: 5vw;
  }
  .maincard img {
    height: 160px;
    width: 160px;
  }
  .item {
    font-family: "Cormorant Garamond", serif;
    font-size: 2vh;
    font-weight: 300;
  }
}
.bg{
  width: 80vw;
  height: 22vh;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  left: 10vw;
  padding: 2vw;
  margin-bottom: 1.5vw;
  border-radius: 3px;
  box-shadow: 0 0 2px rgba(75, 75, 75, 0.07);
}
.tagline{
  color: white;
  font-size: 2vh;
  font-weight: lighter;
  letter-spacing: 1px;
}
#cati{
  color: white;
  float: right;
  font-size: 2vw;
}
.santo{
  color: white;
  letter-spacing: 1vh;
  font-size: 2vw;
}
.bowl{
  width: 3.5vw;
  height: 4vh;
}
.mainmain{
  display: flex;
}
@media (prefers-reduced-motion: no-preference) {
  .bowl {
    animation: App-logo-spin infinite 3s linear;
  }
}

@keyframes App-logo-spin {
  0% {
    transform: rotate(0deg);
  }
  40% {
    transform: rotate(25deg);
  }
  60% {
    transform: rotate(0deg);
  }
  
  80% {
    transform: rotate(-25deg);
  }
  
  90% {
    transform: rotate(-15deg);
  }
  100% {
    transform: rotate(0deg);
  }


}
.pinst{
  display: flex;
}
.Fbox{
  background-color: rgb(148, 243, 127);
  margin-top: 4vh;
  margin-left: 5vw;
  margin-right: 5vw;
  display: flex;
  justify-content: center;
  align-items: center;
}
.Finput{
  width: 20rem;
  height: 2rem;
}
.Fhead{
  color: darkgreen;
  letter-spacing: 2px;
  box-shadow: 2px 2px 2px 2px rgb(36, 190, 6);
}
.Fmain{
  background-color: cadetblue;
  color: rgb(48, 6, 48);
  height: 40px;
}
.Fmain button{
  background-color: aqua;
  height: 50px;
  padding: 2px;
}